header {
  height: 80vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 27%;
  bottom: 25%;
}

.header__socials::after {
  content: "";
  width: 4px;
  height: 1.5rem;
  background: var(--color-primary);
}

.me {
  /* background: linear-gradient(var(--color-primary), transparent); */
  /* background: var(--color-white); */
  height: 13rem;
  width: 22rem;
  /* display: flex;
  justify-content: center; */
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 4rem;
  border-radius: 2rem 2rem 2rem 2rem;
  overflow: hidden;
  /* padding: 1.5rem 1.5rem 1.5rem 1.5rem; */
}

.scroll__down {
  position: absolute;
  right: 25%;
  bottom: 38%;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 1rem;
}

